@tailwind base;
@tailwind components;
@tailwind utilities;
.translate-x {
  transform: translateX(-50%);
}

.img-holder-section {
  width: 100%;
  height: calc(100% - 97px);
  padding: 0 16px 120px;
  position: relative;
  z-index: 0;
}

.img-holder-odd {
  margin-left: auto;
  transform-origin: right center;
  margin-bottom: 50px;
  display: flex;
  align-items: center;
  z-index: 2;
  position: relative;
  max-width: 100%;
  width: fit-content;
  transform: translateX(0px);
}

.img-holder-even {
  translate: none;
  rotate: none;
  scale: none;
  z-index: 1;
  transform: translate(0px, 0px);
  transform-origin: left center;
  margin-right: auto;
  margin-bottom: 50px;
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  z-index: 2;
  position: relative;
  max-width: 100%;
  width: fit-content;
}

.description-container-odd {
  left: 100%;
  text-align: left;
  margin-left: 60px;
  display: flex;
  flex-direction: column;
  position: absolute;
}

.description-container-even {
  right: 100%;
  text-align: left;
  margin-right: 60px;
  display: flex;
  flex-direction: column;
  position: absolute;
}

.description-text {
  width: fit-content;
  margin-left: 10px;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  letter-spacing: -0.005em;
  color: #222;
  margin: 0%;
  padding: 0%;
  font-family: sans-serif;
}

.left-description-arrow {
  transform: rotateY(180deg);
}